import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import WucTab from '@/components/wuc-tab/wuc-tab.vue'
import wsLoadMore from '@/components/wsure-load-more/load-more.vue'
import {
	GetClasslist,GetGoods
} from '../../common/api.js'
export default {
	data() {
		return {
			TabCur: 0,
			pre: 'item',
			failFlag: false,
			count: 0,
			tabList: [],
			last_page:0,
			loaded:false,
			noHave:false,
			pageData:{
				class_id:"",
				page:1,
				limit:20
			},
			goodsArr:[]
		}
	},
	components: {
		uniSearchBar,
		WucTab,
		wsLoadMore
	},
	methods: {
		search() {
			console.log("123")
		},
		pageInit() {
			GetClasslist().then(res => {
				if (res.status) {
					this.tabList = [...res.data];
					this.pageData.class_id = this.tabList[0].id;
					this.getGoodsList();
				}
			})
		},
		getGoodsList(){
			GetGoods(this.pageData).then(res=>{
				if(res.status){
					// console.log(res.data.data.length)
					if(res.data.data.length==0){
						this.noHave = true;
					}else{
						this.last_page = res.data.last_page;
						if(this.pageData.page==this.last_page){
							this.loaded = true;
						}
						for(let i = 0,len=res.data.data.length;i<len;i++){
							this.goodsArr.push(res.data.data[i]);
						}
					}
				}
			})
		},
		tabChange(index, item) {
			this.TabCur = index;
			this.pageData.class_id = item.id;
			this.pageData.page = 1;
			this.loaded = false;
			this.noHave = false;
			this.goodsArr = [];
			this.getGoodsList();
		},
	},
	onLoad() {
		// this.$refs.loadMore.reLoadData()
		this.pageInit();
	},
	// onPullDownRefresh() {
	// 	console.log("onPullDownRefresh")
	// 	this.$refs.loadMore.pullDownRefresh()
	// },
	onReachBottom() {
		this.pageData.page++;
		if(this.pageData.page<=this.last_page){
			// console.log(this.pageData.page)
			this.getGoodsList();
		}else if(this.pageData.page>=this.last_page){
			this.loaded = true;
		}
		// this.$refs.loadMore.reachBottom()
	}
};
